import React, { useState, useEffect } from "react";
import DropdownMenu from "../components/DropdownMenu.jsx";
import DatePicker from "../components/DatePicker.jsx";
import TimePicker from "../components/TimePicker.jsx";
import config from "../config.js";
import { getCityPhoto, getCountryFlag, getLocalDateTimeString } from "../custom-functions.js";

function TimeZoneContainer(props) {
  //time zone container is a container containing a single city, it displays the datetime converted to this city's time zone

        //cityObj containing the indexId and a city object comprimising of all the city details (e.g. name, country, time zone)
  const cityObj = props.cityObj,
        //extracts the city object containins all the city details (e.g. name, country, time zone)
        selectedCity = cityObj.city,
        //cityList from parent element containing a list of all the selected cities in the app
        setCityList = props.setCityList,
        //callDateTime is the master date time controlled by the user, the app displays this time converted to the selected local time zones
        callDateTime = props.callDateTime,
        setCallDateTime = props.setCallDateTime,
        //indexId is a unique id that reprensets every card added throughout the lifetime of the app instance
        indexId = cityObj.indexId,
        //currentTime that is updated at least every second
        currentTime = props.currentTime,
        //detect if this is the main city, main city has indexId = 0, it is first in the cityList, it cannot be removed,
        //and unlike non-main cities - changing main city will cause the master date time to be adjusted
        isMain = cityObj.indexId === 0,
        //index of this city within cityList
        listIndex = props.listIndex,
        //length of cityList
        listLength = props.listLength,
        //function to remove a card from cityList
        removeCard = props.removeCard,
        //photoURL is the URL containing the image of the selected city
        [photoURL, setPhotoURL] = useState(null),
        //countryFlag is the URL containing the flag of the selected city's country
        [countryFlag, setCountryFlag] = useState(null),
        //dateTimeString is the string to be displayed within the card, it converts the master date time to the local time zone
        [dateTimeString, setDateTimeString] = useState(getLocalDateTimeString(callDateTime, selectedCity.timeZone))

  useEffect(() => {
    if (selectedCity.id === "") {
      //if selectedCity has not been set (i.e. blank), set photoURL, countryFlag and dateTimeString to null
      setPhotoURL("");
      setCountryFlag("");
      setDateTimeString(null);
    } else {
      //if selectedCity has been set, get the city photo and country flag
      getCityPhoto(selectedCity, setPhotoURL);
      getCountryFlag(selectedCity.countryCode, setCountryFlag);
    }
  }, [selectedCity])

  useEffect(() => {
    //if callDateTime changes, converts it to local time string and save in state dateTimeString
    if (selectedCity.id === "") { setDateTimeString(null) }
    else { setDateTimeString(getLocalDateTimeString(callDateTime, selectedCity.timeZone)) }
  }, [callDateTime])

  function updateCityList(cityObj) {
    //change the city object contained within a single element of cityList
    setCityList(prev => {
      const newList = prev;
      newList[listIndex].city = cityObj;
      return newList;
    })
  }

  function updateSelectedCity(city) {
    //callback to run when user changes the selected city
    if (city === null) {
      //if no city was specified, output a blank city object
      updateCityList({ id: "", label: "" })
      return
    }
    //retrieve city details from backend based on city id
    const cityId = city.id
    const url = `${config.backendTimeZoneApi}?cityId=${cityId}`;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        if (typeof data === "object" && Object.keys(data).length > 0) {
          const newCity = data;
          updateCityList(newCity);
          //when selectedCity is change: if it is the main city, then change the master date time; else convert the master date time to the selected city's time zone
          if (isMain) { dateTimeString ? updateCallDateTime(dateTimeString, newCity) : setDateTimeString(getLocalDateTimeString(callDateTime, newCity.timeZone))}
          else { setDateTimeString(getLocalDateTimeString(callDateTime, newCity.timeZone)) }
        } else {
          throw TypeError(`Data is of type ${typeof data}.`)
        }
      })
      .catch(err => {
        console.error(err);
      })
  }

  function updateCallDateTime(newCallDateTime, city) {
    //change master call date time
    const latitude = city.latitude;
    const longitude = city.longitude;
    //guess current utcOffset will be the same utcOffset at the new time
    newCallDateTime = newCallDateTime.toString().split("GMT")[0] + ` GMT${city.utcOffset}`;
    let time = Math.trunc(new Date(newCallDateTime).getTime()/1000);
    //fetch and verify the utcOffset at the new time
    const url = `${config.backendUtcOffsetApi}?latitude=${latitude}&longitude=${longitude}&time=${time}`;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        try {
          if (typeof data === "object" && Object.keys(data).length > 0) {
            if (data.utcOffset !== city.utcOffset) {
              //if guessed and actual utcOffset are not equal, adjust the master new time
              newCallDateTime = newCallDateTime.toString().split("GMT")[0] + ` GMT${data.utcOffset}`;
              //update utcOffset in cityList
              updateCityList({ ...selectedCity, utcOffset: data.utcOffset });
            }
            //set master date time state and set dateTimeString state
            setCallDateTime(new Date(newCallDateTime));
            setDateTimeString(getLocalDateTimeString(new Date(newCallDateTime), city.timeZone));
          } else {
            throw TypeError(`Data is of type ${typeof data}.`)
          }
        } catch (e) {
          console.error(e);
          setCallDateTime(new Date(newCallDateTime));
          setDateTimeString(getLocalDateTimeString(new Date(newCallDateTime), city.timeZone));
          alert("Daylight savings could not be verified.");
        }
      })
  }

  function printCurrentDate(currentTime, timeZone) {
    let dateString = currentTime.toLocaleString("en-us", {
      timeZone: timeZone,
      weekday: "long",
      year: "numeric",
      month: "long",
      day: "numeric"
    })
    return `${dateString}`
  }

  function printCurrentTime(currentTime, timeZone) {
    let timeString = currentTime.toLocaleString("en-us", { timeZone: timeZone }).split(" ");
    return `${timeString[timeString.length-2]} ${timeString[timeString.length-1]}`
  }

  function initRemoveCard() {
    removeCard(indexId);
  }

  const blankMode = selectedCity.id === "" ;

  return (
    <div id={`TZ${indexId}`} className="time-zone-container frosted-glass">
      { (listIndex >= 1 && listLength > 2) && <i className="fas fa-trash-alt card-delete" onClick={initRemoveCard}></i>}
      <div className="city-image-container">
        {!blankMode && photoURL ? <img className="city-image" alt={selectedCity.cityName} src={photoURL}/> : null}
      </div>
      {
        blankMode ?
        <div className={`time-zone-content ${!isMain && "sub-content"}`}>
          <div className="dropdown-menu-container">
            <p className="add-instruction">Add a city to convert</p>
            <DropdownMenu selectedCity={selectedCity} updateSelectedCity={updateSelectedCity}/>
          </div>
        </div> :
        <div className={`time-zone-content ${!isMain && "sub-content"}`}>
          <img className="country-flag" alt={selectedCity.countryCode} src={countryFlag}/>
          <p className="time-zone-details">{selectedCity.cityName}, {selectedCity.countryCode}<br/>(GMT{selectedCity.utcOffset})</p>
          <div className="dropdown-menu-container">
            <DropdownMenu selectedCity={selectedCity} updateSelectedCity={updateSelectedCity}/>
          </div>
          <div className="date-time-picker">
            <DatePicker indexId={indexId} selectedCity={selectedCity} callDateTime={dateTimeString} updateCallDateTime={updateCallDateTime}/>
            <TimePicker indexId={indexId} selectedCity={selectedCity} callDateTime={dateTimeString} updateCallDateTime={updateCallDateTime}/>
          </div>
          <div className="current-time-container">
            <p className="current-time current-time-label">Right now in {selectedCity.cityName}</p>
            <p className="current-time">{printCurrentDate(currentTime, selectedCity.timeZone)}</p>
            <p className="current-time">{printCurrentTime(currentTime, selectedCity.timeZone)}</p>
          </div>
        </div>
      }
    </div>
  )
}

export default TimeZoneContainer;
