import React, { useEffect, useState } from "react";
import publicIp from "public-ip";
import "./App.sass";
import config from "./config.js";
import Backdrop from "./components/Backdrop.jsx";
import LoadingScreen from "./components/LoadingScreen.jsx";
import Notification from "./components/Notification.jsx";
import SummaryContainer from "./containers/SummaryContainer.jsx";
import TimeZoneContainer from "./containers/TimeZoneContainer.jsx";

//inteval object used to update the current time at least every second
var updateTimeInterval = null;
//an indexId that will only be incremented, it is a unique id that reprensets every card added throughout the lifetime of the app instance
var indexId = 1;
//an object that indicates the city has not been set by the user or has been cleared by the user
const blankCityObj = { id: "", label: "" };

function App() {

        //callDateTime is the master date time controlled by the user, the app displays this time converted to the selected local time zones
  const [callDateTime, setCallDateTime] = useState(new Date()),
        //currentTime is the current date time, it is updated at least every second to display the current time at each time zone
        [currentTime, setCurrentTime] = useState(new Date()),
        //upon running the app, the app attempts to detect the user's local time zone and city based on the ip address
        //userLocationNotified indicates if the user has been notified about the success and failure of the location detection
        [userLocationNotified, setUserLocationNotified] = useState(false),
        //cityList contains the list of timezones that are set, added or removed by the user
        [cityList, setCityList] = useState([
          {
            indexId: 0,
            city: {}
          },
          {
            indexId: indexId,
            city: blankCityObj
          }])

  useEffect(() => {
    //Upon mounting, get user's city and initiate time interval to update currentTime
    getUserCity();
    clearInterval(updateTimeInterval);
    updateTimeInterval = setInterval(() => setCurrentTime(new Date()), 500);

    //cleanup
    return () => clearInterval(updateTimeInterval);
  }, [])

  async function getUserCity() {
    //Fetch city info from client's IP address
    const url = `${config.backendTimeZoneApi}?ip=${await publicIp.v4()}`;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        if (typeof data === "object" && Object.keys(data).length > 0) {
          setCityList(prevList => {
            const newList = prevList;
            newList[0].city = data;
            return newList;
          })
        }
        else { throw TypeError(`Data is of type ${typeof data}.`) }
      })
      .catch(err => {
        //if unable to fetch, set user's city as a "blank" (i.e. blankCityObj)
        setCityList(prevList => {
          const newList = prevList;
          newList[0].city = blankCityObj;
          return newList;
        })
        console.error(err);
      })
  }

  function addCard() {
    //adds a new card (i.e. new city) to the list
    indexId = indexId + 1;
    setCityList(prev => [...prev, { indexId: indexId, city: blankCityObj }])
  }

  function removeCard(indexIdToRemove) {
    //removes a card from the list
    const arrayIndex = cityList.findIndex(elem => elem.indexId === indexIdToRemove);
    setCityList(prev => {
      prev.splice(arrayIndex, 1)
      return prev
    })
  }

  if (Object.keys(cityList[0].city).length === 0) {
    //if user city has not been set (i.e. while attempting to detect user's location), then display loading screen
    return (
      <div className="app-main full-height">
      <div className="heading">Time Zone Converter</div>
        <div className="loading-main">
          <Backdrop />
          <LoadingScreen />
        </div>
      </div>
    )
  } else {
    //when user city has been set, display main app
    return (
      <div className="app-main">
        <Backdrop />
        {
          //if user has not been notified about outcome of location detection, then notify, else none
          userLocationNotified ?
          null :
          <Notification
            closeCallback={setUserLocationNotified}
            status={cityList[0].city.id !== ""}
            successMessage={`Detected location as ${cityList[0].city.cityName}, ${cityList[0].city.countryCode}.`}
            failureMessage="Unable to detect your location."/>
        }
        <div className="heading">Time Zone Converter</div>
        <div className="sub-heading">Convert time between cities the simple way.<br/> Daylight savings are accounted for.</div>
        <div className="swipe-instruction"><i className="fas fa-arrow-circle-right swipe-icon"></i>Swipe to add more cities </div>
        <div className="time-zone-list">
          {
            //display all cities within cityList
            cityList.map((cityObj, listIndex) => {
            return (
              <div key={cityObj.indexId} className="horizontal-scroll-unit">
                <TimeZoneContainer
                  key={cityObj.indexId}
                  listIndex={listIndex}
                  listLength={cityList.length}
                  cityObj={cityObj}
                  setCityList={setCityList}
                  callDateTime={callDateTime}
                  setCallDateTime={setCallDateTime}
                  currentTime={currentTime}
                  removeCard={removeCard}
                />
              </div>
            )
            })
          }
          {/* a card button that allows user to click to add more cards */}
          <div className="horizontal-scroll-unit">
            <div className="add-card frosted-glass" onClick={addCard}><i className="far fa-plus-square"></i></div>
          </div>
        </div>
        {/* prints a summary of the cities and times when there is more than 1 non-blank city */}
        {cityList.length > 1 && <SummaryContainer cityList={cityList} callDateTime={callDateTime}/>}
    </div>
    );
  }
}

export default App;
