import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Notification from "../components/Notification.jsx";
import ClearIcon from '@material-ui/icons/Clear';
import { getLocalDateTimeString, getDateFromString } from "../custom-functions.js";

const useStyles = makeStyles({
  table: {
    width: "95%",
    minWidth: "0px"
  },
  tableHeader: {
    fontFamily: "inherit",
    fontSize: "1rem"
  },
  tableRow: {
    fontFamily: "inherit",
    fontSize: "0.95rem"
  },
});

function SummaryContainer(props) {
        //upon initiation, filter cityList for only non-blank cities (i.e. cities where id !== "")
  const selectedCityList = props.cityList.filter(cityObj => cityObj.city.id !== ""),
        callDateTime = props.callDateTime,
        //calculates the number of non-blank cities
        numberOfCities = selectedCityList.length,
        classes = useStyles(),
        //state to track whether user wants the summary to be shown or hidden, controlled by user
        [showSummary, setShowSummary] = useState(false),
        //after user presses "Copy" button, copyNotified indicates if the user has been notified that the object was successfully copied
        [copyNotified, setCopyNotified] = useState(true);

  useEffect(() => {
    //if user wishes summary to be shown, scroll to the summary table
    const summaryTable = document.getElementsByClassName("summary-table")[0];
    if (showSummary && summaryTable) {
      navigateToSummaryTable()
    }
  }, [showSummary])

  function printTime(dateTime, timeZone) {
    //extracts the time component in HH:MM AM/PM format of a datetime object (seconds are not included)
    let timeString = dateTime.toLocaleString("en-us", { timeZone: timeZone }).split(" ");
    let secondsRemoved = timeString[timeString.length-2].split(":")
    secondsRemoved.pop()
    secondsRemoved = secondsRemoved.join(":")
    return `${secondsRemoved} ${timeString[timeString.length-1]}`
  }

  function toggleSummaryTable() {
    //toggle between showing and hiding summary table
    if (showSummary) {
      //if instructed to show summary table, play an appearing animation
      const summaryTable = document.getElementsByClassName("summary-table")[0];
      if (summaryTable) {summaryTable.style.animation = "popout ease-out 0.3s forwards"}
      summaryTable.onanimationend = () => setShowSummary(prev => !prev);
    } else {
      //if instructed to hide summary table, toggle showSummary state
      setShowSummary(prev => !prev);
    }
  }

  function navigateToSummaryTable() {
    //scroll to summary table
    const summaryTable = document.getElementsByClassName("summary-table")[0];
    if (summaryTable) {summaryTable.scrollIntoView({ behavior: "smooth", alignToTop: true })}
  }

  function copyToClipboard() {
    //create a string that contains the details of each non-blank city within cityList and copy to user's clipboard
    const summaryTable = document.getElementsByClassName("summary-table")[0];
    if (summaryTable) {
      //crete a textarea element, this is required to allow copy to clipboard
      const textArea = document.createElement("textarea");
      //hide element by setting its position to far left
      textArea.style.position = "absolute";
      textArea.style.left = "-99999px"

      //loop through selectedCityList and append information into array
      let htmlText = [];
      selectedCityList.map(cityObj => {
        const city = cityObj.city;
        const dateString = getDateFromString(getLocalDateTimeString(callDateTime, city.timeZone));
        const timeString = printTime(callDateTime, city.timeZone);
        htmlText.push(`${city.cityName}, ${city.countryCode} (GMT${city.utcOffset}) - ${dateString} at ${timeString}`);
        return null;
      });

      //join array of strings with newline and append to textArea element
      htmlText = htmlText.join("\n");
      textArea.innerHTML = htmlText;
      document.body.appendChild(textArea);

      //select and copy
      textArea.select();
      document.execCommand("copy");

      //remove textArea
      document.body.removeChild(textArea);

      //toggle copyNotified to initiate notification to user that copy has been successful
      setCopyNotified(false);
    }
  }

  if (numberOfCities < 2) {
    //if there aren't at least two non-blank cities, do not display summary container
    return <div/>
  } else {
    return (
      <div className="summary-container">
        {copyNotified ? null : <Notification infoMessage="Copied to clipboard." closeCallback={setCopyNotified} position="bottom"/>}
        {
          showSummary ?
          <ClearIcon className="hide-table frosted-glass" onClick={toggleSummaryTable} /> :
          <button className="show-summary-btn frosted-glass" onClick={toggleSummaryTable}>
            <i className="fas fa-table table-icon"></i> Get Table Summary
          </button>
        }
        {
          showSummary &&
          <TableContainer className="summary-table frosted-glass">
            <Table className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell className={classes.tableHeader}>City</TableCell>
                  <TableCell className={classes.tableHeader} align="right">GMT Offset</TableCell>
                  <TableCell className={classes.tableHeader} align="right">Date</TableCell>
                  <TableCell className={classes.tableHeader} align="right">Time</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {selectedCityList.map(cityObj => {
                  const city = cityObj.city;
                  return (
                    <TableRow key={cityObj.indexId}>
                      <TableCell component="th" scope="row" className={classes.tableRow}>
                        {city.cityName}, {city.countryCode}
                      </TableCell>
                      <TableCell className={classes.tableRow} align="right">{city.utcOffset}</TableCell>
                      <TableCell className={classes.tableRow} align="right">{getDateFromString(getLocalDateTimeString(callDateTime, city.timeZone))}</TableCell>
                      <TableCell className={classes.tableRow} align="right">{printTime(callDateTime, city.timeZone)}</TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
            </Table>
            <div className="copy-btn" onClick={copyToClipboard}><i className="far fa-copy copy-icon" ></i><p>Copy as plain text</p></div>
          </TableContainer>
        }
      </div>
    )
  }
}

export default SummaryContainer;
