import React, { useEffect, useRef, useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { MuiPickersUtilsProvider, KeyboardTimePicker } from "@material-ui/pickers";
import AccessTimeIcon from "@material-ui/icons/AccessTime";
import { makeStyles } from "@material-ui/core/styles";
import { getTimeFromString } from "../custom-functions.js";

const useStyles = makeStyles({
  inputStyles: {
    fontFamily: "inherit",
    color: "inherit",
    width: "95%",
    left: "7px",
    padding: "7px"
  }
});

function TimePicker(props) {
  const classes = useStyles(),
        selectedCity = props.selectedCity,
        callDateTime = props.callDateTime,
        updateCallDateTime = props.updateCallDateTime,
        prevCallDateTime = usePrevious(callDateTime),
        indexId = props.indexId,
        [pickerOpen, setPickerOpen] = useState(false);

  useEffect(() => {
    if (prevCallDateTime && callDateTime) {
      const prevTime = getTimeFromString(prevCallDateTime);
      const currentTime = getTimeFromString(callDateTime);
      if (prevTime !== currentTime) {
        const elem = document.getElementsByClassName(`TP${indexId}`)[0];
        if (elem) {elem.style.animation = "highlight ease-out 0.6s 3"}
      }
    }
  }, [callDateTime])

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

  function handleChange(userInput) {
    if (userInput !== null && userInput.getTime()) {
      updateCallDateTime(userInput, selectedCity);
    }
  }

  function removeAnimation(event) {
    event.target.style.animation = "";
  }

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <KeyboardTimePicker
        className={`TP${indexId}`}
        onClick={() => setPickerOpen(true)}
        onClose={() => setPickerOpen(false)}
        open={pickerOpen}
        value={callDateTime}
        onChange={handleChange}
        keyboardIcon={<AccessTimeIcon />}
        InputProps={{className: classes.inputStyles}}
        onAnimationEnd={removeAnimation}
        KeyboardButtonProps={{
            'aria-label': 'change time',
        }}/>
    </MuiPickersUtilsProvider>
  )
}

export default TimePicker;
