import React, { useEffect, useRef, useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { MuiPickersUtilsProvider, KeyboardDatePicker } from "@material-ui/pickers";
import CalendarTodayIcon from "@material-ui/icons/CalendarToday";
import { makeStyles } from "@material-ui/core/styles";
import { getDateFromString } from "../custom-functions.js";

const useStyles = makeStyles({
  inputStyles: {
    fontFamily: "inherit",
    color: "inherit",
    width: "95%",
    left: "7px",
    padding: "7px"
  }
});

function DatePicker(props) {
  const classes = useStyles(),
        selectedCity = props.selectedCity,
        callDateTime = props.callDateTime,
        updateCallDateTime = props.updateCallDateTime,
        prevCallDateTime = usePrevious(callDateTime),
        indexId = props.indexId,
        [pickerOpen, setPickerOpen] = useState(false);

  useEffect(() => {
    if (prevCallDateTime && callDateTime) {
      const prevDate = getDateFromString(prevCallDateTime);
      const currentDate = getDateFromString(callDateTime);
      if (prevDate !== currentDate) {
        const elem = document.getElementsByClassName(`DP${indexId}`)[0];
        if (elem) { elem.style.animation = "highlight ease-out 0.6s 3" }
      }
    }
  }, [callDateTime])

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

  function handleChange(userInput) {
    if (userInput !== null && userInput.getTime()) {
      updateCallDateTime(userInput, selectedCity);
    }
  }

  function removeAnimation(event) {
    event.target.style.animation = "";
  }

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <KeyboardDatePicker
        format={"EEE, MMM dd, yyyy"}
        onClick={() => setPickerOpen(true)}
        onClose={() => setPickerOpen(false)}
        open={pickerOpen}
        className={`DP${indexId} no-user-select`}
        value={callDateTime}
        onChange={handleChange}
        keyboardIcon={<CalendarTodayIcon />}
        InputProps={{className: classes.inputStyles}}
        onAnimationEnd={removeAnimation}
        KeyboardButtonProps={{
            'aria-label': 'change date',
        }}/>
    </MuiPickersUtilsProvider>
  )
}

export default DatePicker;
