import config from "./config.js";

function getCityPhoto(city, setPhotoURL) {
  const url = `${config.backendCityPhotoApi}?cityName=${city.cityName}&countryName=${city.countryName}`;
  fetch(url)
    .then(res => res.json())
    .then(data => {
      const photoURL = data.photoURL;
      setPhotoURL(photoURL);
    })
}

function getCountryFlag(countryCode, setCountryFlag) {
  const url = `${config.backendCountryFlagApi}?countryCode=${countryCode}`;
  fetch(url)
    .then(res => res.json())
    .then(data => {
      const flagURL = data.flagURL;
      setCountryFlag(flagURL);
    })
}

function getLocalDateTimeString(dateTime, timeZone) {
  return dateTime.toLocaleString("en-us", {
    timeZone: timeZone,
    weekday: "short",
    month: "short",
    day: "2-digit",
    year: "numeric",
    hour: "2-digit",
    minute: "2-digit",
    second: "2-digit",
    hourCycle: "h23" //see https://github.com/moment/luxon/issues/675
  });
}

function getDateFromString(dateTimeString) {
  //Takes a dateTimeString in the format of "{Weekday}, {Date}, {Time}" and return the {Date}
  const splittedString = dateTimeString.split(",");
  //Remove time component, now splittedString is left with just the weekday and date component
  splittedString.pop();
  //Return joined array of strings
  return splittedString.join(",");
}

function getTimeFromString(dateTimeString) {
  //Takes a dateTimeString in the format of "{Weekday}, {Date}, {Time}" and return the {Time}
  const splittedString = dateTimeString.split(",");
  //Gets the time component
  let timeComponent = splittedString[splittedString.length-1];
  //Removes the seconds
  timeComponent = timeComponent.split(":")
  timeComponent.pop();
  //Return joined array of hour and minute
  return timeComponent.join(":");
}

export { getCityPhoto, getCountryFlag, getLocalDateTimeString, getDateFromString, getTimeFromString };
