import React from 'react';
import diagmondsLight from "./images/diagmondsLight.png"

function Backdrop() {
  const stroke = "white",
        strokeWidth = "0",
        fillGradient = "url(#colorGradient)",
        fillImage = "url(#backdropImage)"
        // <stop offset="0%" stopColor="rgba(165, 91, 217, 1)" />
        // <stop offset="50%" stopColor="rgba(149, 0, 255, 1)" />
  return (
    <svg className="backdrop" width="100%" viewBox="0 0 1920 1080">
      <linearGradient id="colorGradient">
        <stop offset="0%" stopColor="rgba(55, 187, 242)" />
        <stop offset="50%" stopColor="rgba(25, 157, 212)" />
      </linearGradient>
      <path
        d="M 0 0 V 450 C 800 500 900 900 1920 700 V 0 H 0"
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill="rgba(255,255,255,1)"
      >
      </path>
      <path
        d="M 0 0 V 400 C 800 500 900 900 1920 600 V 0 H 0"
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill={fillGradient}
      >
      </path>
      <pattern id="backdropImage" patternUnits="userSpaceOnUse" width="141" height="142">
        <image href={diagmondsLight} x="0" y="0" width="141" height="142" />
      </pattern>
      <path
        d="M 0 0 V 400 C 800 500 900 900 1920 600 V 0 H 0"
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill={fillImage}
      >
      </path>
    </svg>
  )
}

export default Backdrop;
