import React, { useEffect, useState } from "react";
import TextField from '@material-ui/core/TextField';
import { Autocomplete, createFilterOptions } from '@material-ui/lab';
import { makeStyles } from '@material-ui/core/styles';
import cityListData from "../city-list-data.js";

const useStyles = makeStyles({
  inputStyles: {
    fontFamily: "inherit",
    fontSize: "1.05rem",
    color: "rgba(20, 20, 20, 1)",
  }
});

const filterOptions = createFilterOptions({
  limit: 50,
});

function DropdownMenu(props) {
  const classes = useStyles(),
        selectedCity = props.selectedCity,
        updateSelectedCity = props.updateSelectedCity,
        [inputValue, setInputValue] = useState(selectedCity.label)

  useEffect(() => {
    selectedCity.label && setInputValue(selectedCity.label);
  }, [selectedCity])

  return (
    <Autocomplete
      options={cityListData}
      filterOptions={filterOptions}
      getOptionLabel={(option) => option.label}
      getOptionSelected={(option, value) => option.label === value.label}
      onChange={(event, userInput) => updateSelectedCity(userInput)}
      inputValue={inputValue}
      onInputChange={(event, userInput) => {setInputValue(userInput)}}
      noOptionsText={`Unable to find city "${inputValue}"`}
      value={selectedCity.label ? selectedCity : null}
      renderInput={(params) =>
        <TextField {...params}
          className={classes.textField}
          label="Location"
          placeholder="Search for city name ..."
          variant="outlined"
          InputProps={{...params.InputProps, classes: {input: classes.inputStyles}}}
        />}
    />
  )
}

export default DropdownMenu;
